@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    font-family: Poppins;
}

:root {
    font-size: 62.5%;
    --red: hsl(0, 100%, 43%);
    --redhover: hsl(0, 100%, 37%);
    --dark: #141414;
    --tdark: hsl(225, 5%, 17%);
    --tdarkhover: hsl(225, 5%, 14%);
    --ldark: hsl(0, 0%, 30%);
    --ldarkhover: hsl(0, 0%, 25%);
    --white: hsl(0, 0%, 92%);
    --whitehover: hsl(0, 0%, 82%);
    --blue : #1E90FF;
}

body {
    background-color: var(--dark);
}

h1 {
    font-size: 4rem;
    padding: 2rem 0;
}

h2 {
    font-size: 3.5rem;
    padding: 1.7rem 0;
}

h3 {
    font-size: 3rem;
    padding: 1.5rem 0;
}

h4 {
    font-size: 2.5rem;
    padding: 1rem 0;
    font-weight: 500;
}

h5 {
    font-size: 2rem;
    padding: 0.8rem 0;
}

h6 {
    font-size: 1.6rem;
    padding: 0.5rem 0;
    font-weight: 400;
}


body::-webkit-scrollbar {
    width: 5px;
}

body::-webkit-scrollbar-thumb {
    background: var(--tdark);
    border-radius: 10px;
    transition: all 0.2s ease;
}

body::-webkit-scrollbar-thumb:hover {
    width: 10px;
    background: var(--red);
}











@media screen and (max-width : 1200px) {
    :root {
        font-size: 54.69%;
    }
}

@media screen and (max-width : 1000px) {
    :root {
        font-size: 46.88%;
    }
}

@media screen and (max-width : 800px) {
    :root {
        font-size: 39.06%;
    }
}


@media screen and (min-width : 2000px) {
    :root {
        font-size: 101.57%;
    }
}