.row-container {
    padding: 2rem 4rem;
    position: relative;
}

.row-wrapper {
    display: flex;
    overflow-x: scroll;
}

.row-wrapper::-webkit-scrollbar {
    display: none;
}






.row-title {
    display: inline-block;
    position: relative;
    padding-right: 30px;
}

.row-title h4 {
    color: var(--white);
    padding: 1.5rem 0 1rem;
    font-weight: 500;
    transition: all 0.3s ease;
    cursor: default;

}

.row-title h6 {
    position: absolute;
    width: 100%;
    color: var(--white);
    font-weight: 500;
    transition: all 0.3s ease;
    top: 30%;
    left: 100%;
    transform: translateX(-100%);
    opacity: 0;
    cursor: pointer;
}

.row-title h4:hover {
    color: var(--red);
}

.row-title h6:hover {
    color: var(--red);
}

.row-title:hover h6 {
    transform: translateX(0%);
    opacity: 1;
}





.img-container {
    min-width: 300px;
    padding-right: 10px;
    text-align: center;
}

.img-container img {
    width: 100%;
    border-radius: 5px;
    height: 162px;
    object-fit: cover;
    cursor: pointer;
}

.img-container.vertical img {
    height: 400px;
}

.img-container h6 {
    color: var(--white);
    cursor: context-menu;
}








.carousel-button-left {
    display: block;
    position: absolute;
    color: var(--red);
    left: 0;
    top: 55%;
    transform: translateY(-50%);
    cursor: pointer;
}

.carousel-button-right {
    display: block;
    position: absolute;
    color: var(--red);
    right: 0;
    top: 55%;
    transform: translateY(-50%);
    cursor: pointer;
}

.carousel-button-left span,
.carousel-button-right span {
    font-size: 3rem;
    padding: 0 0.5rem;
}

.carousel-button-left.hide {
    display: none;
}

.carousel-button-right.hide {
    display: none;
}






@media screen and (min-width : 2000px) {
    .img-container {
        min-width: 400px;
    }

    .img-container img {
        height: 216px;
    }

    .img-container.vertical img {
        height: 534px;
    }



}


@media screen and (max-width : 1200px) {
    .img-container {
        min-width: 250px;
    }

    .img-container img {
        height: 135px;
    }

    .img-container.vertical img {
        height: 334px;
    }


}


@media screen and (max-width : 1000px) {
    .img-container {
        min-width: 200px;
    }

    .img-container img {
        height: 108px;
    }

    .img-container.vertical img {
        height: 267px;
    }


}


@media screen and (max-width : 800px) {
    .img-container {
        min-width: 175px;
    }

    .img-container img {
        height: 95px;
    }

    .img-container.vertical img {
        height: 234px;
    }



}