.error-message {
    padding: 2rem 4rem;
}

.error-message h3 {
    text-align: center;
    color: var(--red);
}

.error-message p {
    color: var(--white);
    font-size: 2rem;
}

.error-message a {
    color: var(--white);
    cursor: pointer;
    transition: all 0.3s ease;
}

.error-message a:hover {
    color: var(--red);
}


.no-page h4 {
    color: var(--red);
    text-align: center;
    padding-top: 10rem;
}