.nav-container {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 4rem;
    align-items: center;
    transition: all 0.2s ease;
    z-index: 5;
}

.nav-bg {
    background-color: var(--dark);
}

.nav-left,
.nav-right {
    display: flex;
    align-items: center;
    position: relative;
}

.nav-right {
    position: relative;
}

.nav-logo {
    width: 14rem;
}

.nav-right .nav-avatar {
    width: 4rem;
    margin-left: 1.5rem;
    cursor: pointer;
    position: relative;
    z-index: 2;
}

.nav-logo img,
.nav-avatar img {
    width: 100%;
    cursor: pointer;
}




.nav-navigation {
    padding: 1rem 7rem 1rem 1rem;
    position: absolute;
    left: 14rem;
    transition: all 0.3s ease;
}



.nav-navigation ul {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.nav-navigation li {
    min-width: fit-content;
    list-style-type: none;
    padding-right: 2rem;
    display: inline-block;
}

.nav-navigation .inactive {
    color: var(--white);
    font-size: 1.6rem;
    text-decoration: none;
    transition: all 0.3s ease;
}

.nav-navigation .active {
    color: var(--red);
    font-size: 1.6rem;
    text-decoration: none;
    transition: all 0.3s ease;
}


.nav-navigation .inactive:hover {
    color: var(--red);
}









.nav-search-container {
    display: flex;
    align-items: center;
    position: relative;
    transition: all 0.5s ease;
}


.nav-input-container {
    width: 400px;
    height: 4rem;
    position: relative;
}


.nav-input-container input[type="text"] {
    position: absolute;
    top: 0;
    background: var(--tdark);
    border: none;
    padding: 0.8rem;
    padding-right: 4.5rem;
    width: 100%;
    outline: none;
    color: var(--white);
    font-size: 1.6rem;
    display: block;
    border-radius: 10px;
    opacity: 0;
    transform: translateX(100%);
    transition: all 0.5s ease;
}

.nav-input-container.show input[type="text"] {
    opacity: 1;
    transform: translateX(0%);
}

.nav-search {
    color: var(--white);
    padding: 0.7rem 1rem 0.5rem;
    cursor: pointer;
    position: absolute;
    right: 0;
    width: 4.5rem;
    height: 4rem;
    border-radius: 50%;
    transition: all 0.5s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.6rem;
}

.nav-search-container.show .nav-search {
    border-radius: 10px;
}





.nav-input-container .nav-input-suggestion-container {
    position: absolute;
    top: 110%;
    color: var(--white);
    background-color: var(--tdark);
    padding: 5px 0px;
    width: 100%;
    height: auto;
    max-height: 500%;
    overflow-y: scroll;
    border-radius: 10px;
    opacity: 0;
    transition: all 1.5s ease;
}

.nav-input-container.show .nav-input-suggestion-container {
    opacity: 1;
}


.nav-input-suggestion-container .nav-input-suggestion {
    padding: 5px 8px;
    font-size: 1.4rem;
    cursor: pointer;
    transition: all 0.5s ease;
}

.nav-input-suggestion-container .nav-input-suggestion:hover {
    background-color: var(--tdarkhover);
}




.nav-input-suggestion-container::-webkit-scrollbar {
    width: 2px;
}

.nav-input-suggestion-container::-webkit-scrollbar-thumb {
    background: var(--white);
    border-radius: 10px;
    transition: all 0.2s ease;
}

.nav-input-suggestion-container::-webkit-scrollbar-thumb:hover {
    background: var(--red);
}















.nav-right .profile-menu {
    background-color: var(--tdark);
    position: absolute;
    right: 0;
    top: 120%;
    width: 15rem;
    border-radius: 10px;
    transform: translateX(200%);
    opacity: 0;
    transition: all 0.3s ease;
}

.nav-right .profile-menu.show {
    transform: translateX(0%);
    opacity: 1;
}

.nav-right .profile-menu li {
    padding: 0.7rem 1rem;
    list-style-type: none;
    font-size: 1.6rem;
    width: 100%;
    cursor: pointer;
    color: var(--white);
    border: none;
}

.nav-right .profile-menu a {
    text-decoration: none;
}

.nav-right .profile-menu li:first-child,
.nav-right .profile-menu li:last-child {
    border-radius: 10px;

}

.nav-right .profile-menu li:hover {
    background-color: var(--tdarkhover);
}












@media screen and (max-width:1200px) {
    .nav-input-container {
        width: 350px;
    }
}


@media screen and (max-width:1000px) {
    .nav-input-container {
        width: 300px;
    }
}


@media screen and (max-width:800px) {
    .nav-input-container {
        width: 200px;
    }
}


@media screen and (max-width:600px) {
    .nav-input-container {
        width: 100px;
    }

    .nav-navigation li {
        padding-right: 1rem;
    }

    .nav-navigation.hide {
        opacity: 0;
    }
}


@media screen and (max-width:400px) {

    .nav-navigation ul {
        width: 24rem;
        flex-wrap: wrap;
    }
}