.sign-background {
    position: relative;
    color: var(--white);
    height: 100vh;
    width: 100%;
    background-image: url(./../../img/banner.jpg);
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    text-align: center;
}

.sign-gradient {
    position: absolute;
    inset: 0;
    background: rgba(0, 0, 0, 0.35);
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0,
            rgba(0, 0, 0, 0) 60%, rgba(0, 0, 0, 0.8) 100%);
    z-index: 1;
}

.sign-nav {
    position: absolute;
    z-index: 2;
    top: 0;
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 5rem;
}










.sign-details {
    position: absolute;
    width: 100%;
    z-index: 2;
    top: 55%;
    transform: translateY(-50%);
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.sign-details h3 {
    font-weight: 700;
    padding: 8px 0;
}

.sign-details h5 {
    font-weight: 600;
    font-size: 2.3rem;
    padding: 8px 0;
}

.sign-details h6 {
    font-weight: 300;
    padding: 8px 0;
}

.sign-details input[type="text"],
.sign-details input[type="password"] {
    background: rgba(0, 0, 0, 0.35);
    border: 1px solid var(--white);
    border-radius: 5px;
    padding: 10px;
    width: 350px;
    outline: none;
    color: var(--white);
    margin: 10px 0;
    font-size: 1.6rem;
}

.sign-up-details.show input[type="text"],
.sign-up-details.show input[type="password"] {
    display: block;
}

.sign-details button {
    min-width: 150px;
}

.btn {
    border-radius: 5px;
    padding: 10px;
    background-color: var(--red);
    color: var(--white);
    outline: none;
    margin: 10px 5px;
    border: none;
    font-size: 1.6rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2S ease;
}

.btn:hover {
    background-color: var(--redhover);
}











.sign-in-box {
    position: absolute;
    z-index: 2;
    text-align: center;
    padding: 5rem;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(0, 0, 0, 0.6);
}

.sign-in-box h4, .sign-in-box h6 {
    text-align: left;
}

.sign-in-box input[type="text"],
.sign-in-box input[type="password"] {
    background: rgba(0, 0, 0, 0.35);
    border: 1px solid var(--white);
    border-radius: 5px;
    padding: 10px;
    width: 350px;
    outline: none;
    color: var(--white);
    margin: 10px 0;
    font-size: 1.6rem;
    display: block;
}

.sign-in-box button {
    width: 100%;
}

.sign-in-box p {
    margin: 10px 0;
    text-align: left;
}

.sign-in-box span {
    color: var(--ldark);
    font-size: 1.4rem;
    margin: 0 3px;
}

.sign-in-box .sign-up {
    color: var(--white);
    border-bottom: 1px solid transparent;
    cursor: pointer;
    transition: all 0.2s ease;
}

.sign-in-box .sign-up:hover {
    border-color: var(--white)
}


.sign-in-box .error {
    color: var(--red);
    font-size: 1.6rem;
    text-align: center;
}















@media screen and (max-width : 600px) {

    .sign-details input[type="text"],
    .sign-details input[type="password"] {
        width: 200px;
    }
}







@media screen and (max-width : 550px) {


    .sign-in-box input[type="text"],
    .sign-in-box input[type="password"] {
        width: 250px;
    }
}