.banner-container {
    position: relative;
    max-height: 95vh;
}

.banner-picture img {
    width: 100%;
    max-height: 95vh;
    object-fit: cover;
    object-position: 50% 25%;
}

.banner-info {
    position: absolute;
    width: 100%;
    bottom: 5%;
    padding: 1rem 4rem;
    color: var(--white);
    z-index: 2;
}

.banner-movie-name h3 {
    font-weight: 700;
    cursor: context-menu;
}

.banner-half {
    width: 30%;
}

.banner-buttons button {
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 5px;
    padding: 8px 18px;
    margin-bottom: 10px;
    transition: all 0.2s ease;
    font-size: 1.6rem;
}

.banner-button-1 {
    margin-right: 20px;
    background-color: var(--white);
    color: black;
}

.banner-button-2 {
    background-color: var(--ldark);
    color: white;
}

.banner-button-1:hover {
    background-color: var(--whitehover);
}

.banner-button-2:hover {
    background-color: var(--ldarkhover);
}



.banner-description {
    line-height: 1.4;
    padding: 1rem 0;
}

.banner-fade {
    position: absolute;
    inset: 0;
    z-index: 1;
    background: linear-gradient(0deg, rgba(20, 20, 20, 1) 0%, rgba(20, 20, 20, 0.6) 25%, rgba(9, 9, 121, 0) 35%, rgba(6, 81, 168, 0) 58%, rgba(20, 20, 20, 0.6) 80%, rgba(20, 20, 20, 1) 100%);
}







@media screen and (min-width:1600px) {

    .banner-picture {
        display: flex;
        justify-content: flex-end;
        position: relative;
    }

    .banner-fade2 {
        position: absolute;
        width: 50%;
        height: 100%;
        right: 0;
        z-index: 1;
        background: linear-gradient(90deg, rgba(20, 20, 20, 1) 0%, rgba(20, 20, 20, 0.6) 25%, rgba(9, 9, 121, 0) 35%, rgba(6, 81, 168, 0) 58%, rgba(20, 20, 20, 0.3) 80%, rgba(20, 20, 20, 0.5) 100%);
    }

    .banner-picture img {
        width: 50%;
    }

}


@media screen and (max-width:1200px) {
    .banner-half {
        width: 50%;
    }
}


@media screen and (max-width:1000px) {
    .banner-half {
        width: 60%;
    }
}

@media screen and (max-width:900px) {
    .banner-half {
        width: 70%;
    }
}

@media screen and (max-width:600px) {
    .banner-half {
        width: 80%;
    }

}

@media screen and (max-width:500px) {
    .banner-description {
        display: none;
    }
}