.video-container{
    background-color: var(--dark);
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 7;
}

.video-container video{
    width: 100%;
    height: 100%;
}

.video-name{
    position: absolute;
    z-index: 8;
    color: var(--white);
    top: 2%;
    left: 10%;
}