.search-result-previewer {
    padding: 8rem 4rem 6rem;
}



.result-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.result-movie-wrapper {
    padding: 20px 10px;
    width: 275px;
    color: var(--white);
}

.result-movie-img-wrapper {
    width: 100%;
}

.result-movie-img-wrapper img {
    width: 100%;
    height: 382px;
    object-fit: cover;
}

.result-movie-title-release {
    text-align: center;
}

.result-movie-content {
    cursor: pointer;
}





.morepage-previewer {
    display: flex;
    justify-content: center;
    padding: 1rem;
    flex-wrap: wrap;
}

.morepage-block-wrapper {
    margin: 5px;
}


.morepage-block {
    color: var(--white);
    background: var(--transparent);
    width: 3.5rem;
    height: 3.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    border: 1px solid white;
    cursor: pointer;
    border-radius: 4px;
    transition: all 0.2s ease;
}

.morepage-block.special {
    width: 7rem;
    background-color: var(--red);
    border-color: transparent;
    transition: all 0.2s ease;
    text-align: center;
}

.morepage-block:hover {
    background-color: var(--tdark);
}

.morepage-block.special:hover {
    background-color: var(--redhover);

}

.morepage-block.active {
    background-color: var(--red);
    border-color: transparent;
    transition: all 0.2s ease;
}

.morepage-block.active:hover {
    background-color: var(--redhover);
}

.morepage-block:not(:last-child) {
    border-right: 1px solid white;
}











@media screen and (min-width:2000px) {

    .result-movie-wrapper {
        width: 350px;
    }

    .result-movie-img-wrapper img {
        height: 450px;
    }
}


@media screen and (max-width:1000px) {
    .result-movie-wrapper {
        width: 200px;
    }

    .result-movie-img-wrapper img {
        height: 300px;
    }

}



@media screen and (max-width:500px) {
    .result-movie-wrapper {
        width: 150px;
    }

    .result-movie-img-wrapper img {
        height: 200px;
    }

    .morepage-block-wrapper {
        margin: 5px 2px;
    }

}