.search-sidebar-container::-webkit-scrollbar {
    width: 2px;
}

.search-sidebar-container::-webkit-scrollbar-thumb {
    background: var(--tdark);
    border-radius: 10px;
    transition: all 0.2s ease;
}

.search-sidebar-container::-webkit-scrollbar-thumb:hover {
    background: var(--red);
}





select::-webkit-scrollbar {
    width: 4px;
}

select::-webkit-scrollbar-thumb {
    background: var(--blue);
    border-radius: 10px;
    transition: all 0.2s ease;
}

select::-webkit-scrollbar-thumb:hover {
    background: var(--blue);
}





.search-sidebar-container {
    color: var(--white);
    font-size: 1.6rem;
    position: fixed;
    top: 10rem;
    left: 0;
    width: 275px;
    padding: 1rem 2rem 3rem;
    height: 90%;
    overflow-y: scroll;
}

.search-sidebar-wrapper {
    padding: 2rem 0;
    border-bottom: 1px solid var(--tdark);
}

.search-sidebar-wrapper input[type="checkbox"],
.search-sidebar-wrapper input[type="radio"] {
    margin-right: 8px;
    accent-color: var(--red);
    font-size: 1.6rem;
}

.search-sidebar-wrapper input[type="text"],
.search-sidebar-wrapper input[type="number"],
.search-sidebar-wrapper input[type="date"],
.search-sidebar-wrapper select {
    background-color: var(--tdark);
    color: var(--white);
    outline: none;
    border: none;
    transition: all 0.2s ease;
    border-radius: 5px;
    font-size: 1.4rem;
}

.search-sidebar-wrapper input[type="number"]:hover,
.search-sidebar-wrapper select:hover,
.search-sidebar-wrapper input[type="text"]:hover {
    background-color: var(--tdarkhover);
}

.search-sidebar-wrapper input[type="text"],
.search-sidebar-wrapper select {
    padding: 0.5rem;
    width: 90%;
}

.search-sidebar-wrapper input[type="date"] {
    padding: 0 1rem;
    width: 90%;
}

.search-sidebar-wrapper .title {
    margin-bottom: 8px;
}

::-webkit-calendar-picker-indicator {
    background-color: var(--tdark);
    padding: 10px 0;
    position: relative;
}




.search-sidebar-wrapper option:hover {
    background-color: var(--red);
}


::placeholder {
    color: var(--white);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus input:-webkit-autofill,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
    -webkit-text-fill-color: rgb(255, 255, 255);
    -webkit-box-shadow: 0 0 0px 1000px transparent inset;
    transition: background-color 5000s ease-in-out 0s;
}


.btn-wrapper {
    display: flex;
    justify-content: center;
}

.btn {
    padding: 10px;
}








.genres-wrapper label {
    margin-right: 10px;
}

.genre-box {
    display: none;
    padding: 10px 0;
}

.genre-box.show {
    display: block;
}

.genres-wrapper span {
    transition: all 0.2s ease;
}

.genres-wrapper span.show {
    transform: rotate(-180deg);
    transition: all 0.2s ease;
}

.genre-box-signal {
    background-color: var(--tdark);
    padding: 5px;
    display: flex;
    justify-content: space-between;
    transition: all 0.2s ease;
    border-radius: 5px;
    width: 90%;
    cursor: pointer;
}

.genre-box-signal:hover {
    background-color: var(--tdarkhover);
}

.genre-box-signal.show {
    background-color: var(--red);
}

.genre-box-signal .dropdown {
    font-size: 12px;
}











.search-sidebar-wrapper .length-selector {
    width: 90%;
}

.search-sidebar-wrapper .price-input {
    width: 100%;
    display: flex;
    margin: 10px 0;
}

.search-sidebar-wrapper .price-input .field {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.search-sidebar-wrapper .field input {
    width: 40px;
    height: 32px;
    margin-left: 8px;
    outline: none;
    border-radius: 5px;
    text-align: center;
    -moz-appearance: textfield;
}

.search-sidebar-wrapper input[type="number"]::-webkit-outer-spin-button,
.search-sidebar-wrapper input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

.search-sidebar-wrapper .price-input .separator {
    padding: 10px 5px 10px 3px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-sidebar-wrapper .slider {
    height: 5px;
    position: relative;
    background: var(--white);
    border-radius: 5px;
}

.search-sidebar-wrapper .slider .progress {
    height: 100%;
    left: 3%;
    right: 50%;
    position: absolute;
    border-radius: 5px;
    background: var(--red);
}

.search-sidebar-wrapper .range-input {
    position: relative;
}

.search-sidebar-wrapper .range-input input {
    position: absolute;
    width: 100%;
    height: 5px;
    top: -5px;
    background: none;
    pointer-events: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.search-sidebar-wrapper input[type="range"]::-webkit-slider-thumb {
    height: 17px;
    width: 17px;
    border-radius: 50%;
    background: var(--red);
    pointer-events: auto;
    -webkit-appearance: none;
}

.search-sidebar-wrapper input[type="range"]::-moz-range-thumb {
    height: 17px;
    width: 17px;
    border: none;
    border-radius: 50%;
    background: var(--red);
    pointer-events: auto;
    -moz-appearance: none;
}







.search-sidebar-wrapper .date-title-1 {
    margin-top: 5px;
    font-size: 1.4rem;
}

.search-sidebar-wrapper .date-title-2 {
    margin-top: 10px;
    font-size: 1.4rem;
}







.language-wrapper {
    display: flex;
    align-items: center;
}


.question-mark-cover {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: var(--tdark);
    margin-left: 1rem;
    position: relative;
}

.question-mark {
    font-size: 1.2rem;
}

.question-content {
    position: absolute;
    top: 100%;
    right: 100%;
    width: 1000%;
    background-color: var(--ldark);
    border-radius: 5px;
    padding: 1rem;
    font-size: 1.3rem;
    display: none;
}

.question-mark-cover:hover .question-content {
    display: block;
}












@media screen and (min-width : 2000px) {
    .search-sidebar-container {
        width: 450px;
    }

    .search-sidebar-wrapper .field input {
        width: 50px;
        height: 42px;
    }
}

@media screen and (max-width : 1200px) {
    .search-sidebar-container {
        font-size: 16px;
    }

    .search-sidebar-wrapper input[type="checkbox"],
    .search-sidebar-wrapper input[type="radio"] {
        font-size: 16px;
    }

    .search-sidebar-wrapper input[type="text"],
    .search-sidebar-wrapper input[type="number"],
    .search-sidebar-wrapper input[type="date"],
    .search-sidebar-wrapper select {
        font-size: 14px;
    }

    .search-sidebar-wrapper {
        padding: 20px 0;
        border-bottom: 1px solid var(--tdark);
    }
}




@media screen and (max-width : 700px) {
    .search-sidebar-container {
        width: 175px;
        font-size: 12px;
    }

    .search-sidebar-wrapper input[type="checkbox"],
    .search-sidebar-wrapper input[type="radio"] {
        font-size: 12px;
    }

    .search-sidebar-wrapper input[type="text"],
    .search-sidebar-wrapper input[type="number"],
    .search-sidebar-wrapper input[type="date"],
    .search-sidebar-wrapper select {
        font-size: 12px;
    }


    .search-sidebar-wrapper .field input {
        width: 30px;
        height: 22px;
    }

    .question-content {
        right: -120%;
    }
}