.poster-container {
    width: 100%;
    position: relative;
    display: flex;
}


.poster-fade-2 {
    position: absolute;
    inset: 0;
    background: rgb(0, 0, 0);
    background: linear-gradient(90deg, rgb(20, 20, 20) 55%, rgba(20, 20, 20, 0.5) 85%, rgba(20, 20, 20, 0) 100%);
    z-index: 2;
}



.poster-fade {
    position: absolute;
    inset: 0;
    z-index: 1;
    background-image: linear-gradient(180deg, rgba(20, 20, 20, 0.5), transparent, transparent, transparent, rgba(20, 20, 20, 0.1), #111);
}






.poster-picture {
    width: 60%;
}

.poster-picture img {
    width: 100%;
    object-position: 100% 25%;
    object-fit: cover;
    min-height: 400px;
    height: 100%;
}












.poster-info {
    width: 40%;
    top: 50%;
    padding: 70px 0 0 4rem;
    color: var(--white);
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
}








.poster-flex {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.poster-popularity,
.poster-release,
.poster-runtime {
    padding: 8px 8px 8px 0;
    font-size: 1.6rem;
    list-style-type: none;
}

.poster-release {
    padding-right: 50px;
}

.poster-runtime {
    opacity: 0;
    animation: fade 100ms ease 290ms forwards;
}

@keyframes fade {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}






.poster-movie-name,
.poster-movie-tagline {
    width: 80vw;
}

.poster-movie-tagline h5 {
    font-weight: 400;
    padding: 0.3rem;
    color: var(--red);
}


.poster-description {
    line-height: 1.4;
    padding: 1rem 0;
}

.poster-description.show {
    display: none;
    padding: 4rem;
    color: var(--white);
}








.poster-buttons button {
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 5px;
    padding: 0.8rem 1.8rem;
    margin-bottom: 1rem;
    margin-top: 1rem;
    transition: all 0.2s ease;
    font-size: 1.6rem;
}

.poster-button-1 {
    margin-right: 2rem;
    background-color: var(--white);
    color: black;
}

.poster-button-2 {
    background-color: var(--ldark);
    color: white;
}

.poster-button-1:hover {
    background-color: var(--whitehover);
}

.poster-button-2:hover {
    background-color: var(--ldarkhover);
}














.poster-release .movie-year-container {
    position: relative;
    margin-left: 4px;
}

.poster-release .movie-year {
    position: absolute;
    opacity: 1;
    top: 0%;
    transition: all 0.4s ease;
}

.poster-release .movie-year.hide {
    opacity: 0;
    top: 100%;
    transition: all 0s ease;
}

.poster-release .movie-year2 {
    position: absolute;
    top: 100%;
    left: 0;
    opacity: 0;
    transition: all 0s ease;
}

.poster-release .movie-year2.show {
    top: 0%;
    transition: all 0.4s ease;
    opacity: 1;
}










.poster-runtime li {
    position: relative;
    display: inline-block;
}

.poster-runtime .red-shift-runtime {
    position: absolute;
    box-sizing: content-box;
    width: 0%;
    height: 100%;
    right: -20%;
    background-color: var(--red);
    transition: all 0.3s ease;
}

.poster-runtime .red-shift-runtime.show {
    width: 140%;
    left: -20%;
}





.popularity-outer-circle {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
    position: relative;
}

.popularity-outer-circle::before {
    content: "";
    position: absolute;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    background-color: var(--ldark);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}


.popularity-percentage {
    position: absolute;
    z-index: 2;
    color: var(--white);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}









@media screen and (min-width : 2000px) {

    .poster-flex {
        width: 70%;
    }

}



@media screen and (max-width : 900px) {

    .poster-picture {
        width: 45%;
    }

    .poster-info {
        width: 55%;
    }


}

@media screen and (max-width : 550px) {
    .poster-description.hide {
        display: none;
    }

    .poster-description.show {
        display: block;
    }

    .poster-flex {
        flex-direction: column;
        align-items: flex-start;
    }
}





















.poster-trailer-container {
    padding: 2rem 4rem;
    font-size: 1.6rem;
    display: flex;
    color: var(--white);
    align-items: center;
}

.youtube-container {
    width: 50%;
    padding: 1.5rem 0;
}

.youtube-container iframe {
    width: 100%;
    height: 25vw;
}

.poster-extra-info {
    width: 50%;
    padding: 1.5rem 6rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}


.poster-genres,
.poster-original-language,
.poster-spoken-language,
.poster-production-countries,
.poster-production-companies {
    padding: 2rem 1rem;
    min-width: 50%;
}

.poster-genres li:first-child,
.youtube-container li:first-child,
.poster-original-language li:first-child,
.poster-spoken-language li:first-child,
.poster-production-countries li:first-child,
.poster-production-companies li:first-child {
    list-style-type: none;
    padding-bottom: 10px;
}

.youtube-container li:first-child {
    margin-bottom: 8px;
}






@media screen and (max-width:1200px) {
    .youtube-container {
        width: 60%;
    }

    .poster-extra-info {
        width: 40%;
    }

    .youtube-container iframe {
        height: 30vw;
    }

}

@media screen and (max-width:800px) {
    .poster-trailer-container {
        flex-direction: column;
        align-items: center;
    }

    .youtube-container {
        width: 75%;
    }

    .youtube-container iframe {
        height: 38vw;
    }

    .poster-extra-info {
        justify-content: space-between;
        width: 100%;
    }

    .poster-genres,
    .poster-original-language,
    .poster-spoken-language,
    .poster-production-countries,
    .poster-production-companies {
        min-width: 25%;
    }
}





@media screen and (max-width:500px) {
    .poster-trailer-container {
        flex-direction: column;
        align-items: center;
    }

    .youtube-container {
        width: 100%;
    }

    .youtube-container iframe {
        height: 50vw;
    }

    .poster-extra-info {
        padding: 15px;
    }

    .poster-genres,
    .poster-original-language,
    .poster-spoken-language,
    .poster-production-countries,
    .poster-production-companies {
        min-width: 50%;
    }
}