
.genre-wrapper{
    padding: 10rem 0 0 0;
}


.genre-special-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 4rem 4rem 2rem;
}

.genre-special-container .genre-movie-wrapper {
    padding: 20px 10px;
    width: 275px;
    color: var(--white);
}

.genre-movie-img-wrapper {
    width: 100%;
}

.genre-special-container .genre-movie-img-wrapper img {
    width: 100%;
    height: 300px;
    object-fit: cover;
    border-radius: 5px;
    cursor: pointer;
}

.genre-movie-title-release {
    text-align: center;
}



.genre-special-container.horizontal .genre-movie-wrapper {
    padding: 20px 10px;
    width: 300px;
    color: var(--white);
}

.genre-special-container.horizontal .genre-movie-img-wrapper img {
    width: 100%;
    height: 162px;
    object-fit: cover;
}











.genre-special-vertical-button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.genre-special-vertical-button h5 {
    color: var(--white);
    padding: 8px 20px;
}


.genre-special-title {
    color: var(--white);
    text-align: center;
}

.genre-special-vertical-button .switch {
    position: relative;
    display: inline-block;
    width: 45px;
    height: 24px;
}

.genre-special-vertical-button .switch input {
    position: absolute;
    width: 100%;
    height: 100%;
}

.genre-special-vertical-button .slider {
    position: absolute;
    cursor: pointer;
    width: 100%;
    height: 100%;
    background-color: var(--ldark);
    transition: .4s;
}

.genre-special-vertical-button .slider:before {
    position: absolute;
    content: "";
    height: 18px;
    width: 18px;
    left: 5px;
    bottom: 3px;
    background-color: var(--white);
    transition: .4s;
}

.genre-special-vertical-button input:checked+.slider {
    background-color: var(--red);
}

.genre-special-vertical-button input:focus+.slider {
    box-shadow: 0 0 1px var(--redhover);
}

.genre-special-vertical-button input:checked+.slider:before {

    transform: translateX(18px);
}

.genre-special-vertical-button .slider.round {
    border-radius: 24px;
}

.genre-special-vertical-button .slider.round:before {
    border-radius: 50%;
}










.load-more {
    display: flex;
    justify-content: center;
}





@media screen and (min-width:2000px) {
    .genre-special-container .genre-movie-wrapper {
        width: 350px;
    }

    .genre-special-container .genre-movie-img-wrapper img {
        height: 381px;
    }

    .genre-special-container.horizontal .genre-movie-wrapper {
        width: 375px;
    }
    
    .genre-special-container.horizontal .genre-movie-img-wrapper img {
        height: 202px;
    }

}


@media screen and (max-width:1000px) {
    .genre-special-container .genre-movie-wrapper {
        width: 200px;
    }

    .genre-special-container .genre-movie-img-wrapper img {
        height: 218px;
    }

    .genre-special-container.horizontal .genre-movie-wrapper {
        width: 225px;
    }
    
    .genre-special-container.horizontal .genre-movie-img-wrapper img {
        height: 121px;
    }

}



@media screen and (max-width:500px) {
    .genre-special-container .genre-movie-wrapper {
        width: 100px;
    }

    .genre-special-container .genre-movie-img-wrapper img {
        height: 114px;
    }

    .genre-special-container.horizontal .genre-movie-wrapper {
        width: 150px;
    }
    
    .genre-special-container.horizontal .genre-movie-img-wrapper img {
        height: 80px;
    }

}