:root {
    --slider-range: var(--white);
    --slider-range-select: var(--red);
    /* --slider-min-max-font: 15px; */
    /* --slider-min-max-digit-font: 14px; */
}







.slider-container {
    width: 90%;
}

.slider-container .price-input {
    width: 100%;
    display: flex;
    padding-bottom: 5px;
}

.slider-container .price-input .field {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}

.slider-container .price-input .field span {
    font-size: var(--slider-min-max-font);
}

.slider-container .field input {
    font-size: var(--slider-min-max-digit-font);
    border: none;
    outline: none;
    text-align: center;
    border-radius: 5px;
    border: 1px solid black;
}


.slider-container input[type="number"]::-webkit-outer-spin-button,
.slider-container input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
}


.slider-container .price-input .separator {
    font-size: var(--slider-min-max-font);
    margin: 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}










.slider-container .slider {
    height: 3px;
    position: relative;
    background: var(--slider-range);
    border-radius: 5px;
}

.slider-container .slider .progress {
    position: absolute;
    height: 100%;
    left: 0%;
    right: 50%;
    border-radius: 5px;
    background: var(--slider-range-select);
}

.slider-container .range-input {
    position: relative;
}

.slider-container .range-input input {
    position: absolute;
    width: 100%;
    height: 3px;
    top: -4px;
    background: none;
    pointer-events: none;
    -webkit-appearance: none;
    -moz-appearance: none;
}

.slider-container input[type="range"]::-webkit-slider-thumb {
    height: 13px;
    width: 13px;
    border-radius: 50%;
    background: var(--slider-range-select);
    pointer-events: auto;
    -webkit-appearance: none;
}

.slider-container input[type="range"]::-moz-range-thumb {
    height: 13px;
    width: 13px;
    border: none;
    border-radius: 50%;
    background: var(--slider-range-select);
    pointer-events: auto;
    -moz-appearance: none;
}